<template>
  <div class="cart-bottom">
    <div>
      <check-button @click.native="selectClick" :is-check="isSelectAll"/>
    </div>
    <div>全选</div>
    <div>合计：￥{{totalPrice}}</div>
    <div>去计算({{totalCount}})</div>
  </div>
</template>
<script>
import CheckButton from 'components/content/checkButton/CheckButton'

import {mapGetters} from 'vuex'

export default {
  name: 'CartBottom',
  components: {
    CheckButton
  },
  data() {
    return {
     
    }
  },
  computed: {
    ...mapGetters(['cartListLength','cartList']),
    totalPrice(){
      return this.cartList.filter(item => 
      item.checked === true).reduce((prev,item) => prev + item.price * item.count,0).toFixed(2)
    },
    totalCount(){
      return this.cartList.filter(item => item.checked === true).reduce((prev,item) => prev + item.count,0)
    },
    isSelectAll(){
      if(!this.cartListLength) return false
      return !this.cartList.filter(item => item.checked === false).length
      // return !this.cartList.find(item => item.checked === false)
    }
  },
  methods: {
    selectClick(){
      if(this.isSelectAll){
        for(let item of this.cartList){
          item.checked = false
        }
      }else {
        for(let item of this.cartList){
          item.checked = true
        }
      }
    }
  }
}
</script>
<style scoped>
.cart-bottom {
  display: flex;
  align-items: center;
  height: 40px;
  padding-left: 10px;

  position: relative;
  bottom: -2px;
  left: 0;
  right: 0;

  background-color: #eee;
}

.cart-bottom div:first-child {
  width: 20px;
}

.cart-bottom div:nth-child(2) {
  width: 50px;
  padding-left: 3px;
}
.cart-bottom div:nth-child(3) {
  flex: 1;
}
.cart-bottom div:nth-child(4) {
  width: 100px;
  color: #fff;
  background-color: orangered;
  text-align: center;
  padding: 10px;
}
</style>