<template>
  <div class="item">
    <div class="button">
      <check-button :is-check="cartListItem.checked" @click.native=" buttonClick" />
    </div>
    <div class="photo">
      <img :src="cartListItem.image" alt="">
    </div>
    <div class="other">
      <p class="title">{{cartListItem.title}}</p>
      <p class="desc">{{cartListItem.desc}}</p>
      <div class="clear-fix"> 
        <span class="price">￥{{cartListItem.price}}</span>
        <span class="count right">x{{cartListItem.count}}</span>
      </div>
    </div>
  </div>
</template>
<script>
import CheckButton from 'components/content/checkButton/CheckButton'

export default {
  name: 'CartListItem',
  components: {
    CheckButton
  },
  data() {
    return {
    }
  },
  props: {
    cartListItem: {
      type: Object,
      default(){
        return {}
      }
    }
  },
  methods: {
      buttonClick(){
      this.cartListItem.checked = !this.cartListItem.checked
    }
  }
}
</script>
