.item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
  padding: 10px 5px;
}
.button {
  flex: 1;
}
.photo {
  flex: 3;
}
.other {
  flex: 9;
  min-width: 0;
  padding: 0 10px;
}
.photo img {
  display: block;
  width: 100%;
  border-radius: 5px;
}
.other p {
  overflow: hidden;
  text-overflow: ellipsis; 
  white-space: nowrap;
}
.desc {
  font-size: 14px;
  color: #999;
  padding: 15px 0 30px;
}

.price {
  font-size: 18px;
  color: orangered;
}
</style>